  // });

  const { data: trendingBrands, isLoading: trendingBrandsLoading } = useGetShopsQuery(
    { page: 1, limit: 5 },
    { refetchOnMountOrArgChange: 300 }
  );
  // useEffect(() => {
  //   // console.log(trendingBrands, "YYUUTT");
  //   if (token) {
  //     // if (token) dispatch(getUserInfo({ token }));
  //     dispatch(getUserCollection({ token }));
  //   }
  // }, [token]);
